@import url(https://fonts.googleapis.com/css2?family=Michroma&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.filtersGroup {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.filtersGroup p {
  font-style: bold;
  font-weight: 650;
  margin: 15px 0;
}
.filtersGroup button {
  width: 50%;
  align-self: center;
}

.myGrid {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.imageGridEl {
  padding-top: 30px;
  grid-column: 1/2;
  grid-row: 1/2;
}

.descriptions {
  grid-column: 2/3;
  grid-row: 1/3;
  display: "flex";
  flex-direction: column;
}

.buttonsBox {
  margin: 25px auto;
  grid-column: 2;
  display: flex;
  justify-content: space-around;
  width: 80%;
}
.btnCHatStart {
  background-color: #8bc6ec;
  background-image: linear-gradient(135deg, #8bc6ec 0%, #9599e2 100%);
  border-radius: 15px;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border: none;
  font-weight: 700;
}

.btnMatchStart {
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgba(174, 68, 223, 1) 27.3%,
    rgba(246, 135, 135, 1) 112.7%
  );
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border: none;
  font-weight: 700;
}

.btnsave {
  background-color: #4c82a7;
  color: white;
  margin: 10px 0;
  width: 100%;
  border: none;
  font-weight: 700;
}
.btnsave:hover {
  color: white;
  background-color: #74b7e4;
}

@media only screen and (max-width: 768px) {
  .myGrid {
    width: 100%;
    margin: 0 auto;
    display: grid;
    height: 100%;
    grid-template-columns: 1fr;
  }

  .imageGridEl {
    padding-top: 30px;
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .descriptions {
    grid-column: 1/2;
    grid-row: 2/3;
    display: "flex";

    flex-direction: column;
  }
  .buttonsBox {
    grid-column: 1/2;

    grid-row: 3/4;
    margin: 15px auto;

    display: flex;
    justify-content: space-around;
    width: 100%;
  }
}



.LayoutBackgorund {
  background-color: red;
  min-height: 100vh;
  background-color: #57a1f9;
  background-image: linear-gradient(62deg, #57a1f9 0%, #e99af2 100%);

  display: flex;
  justify-content: center;
  align-items: center;
}

.transperantBackground {
  background-color: white;

  min-height: 80vh;
  width: 60%;
  border-radius: 20px;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  z-index: 1;
  display: flex;
}

.leftMenu {
  flex: 1 1;
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  padding: 25px;
  border-radius: 2rem;
}

.Menu {
  border-radius: 20px;
  background-color: #57a1f9;
  background-image: linear-gradient(62deg, #57a1f9 0%, #e99af2 100%);

  margin: 0 auto;
  padding: 55px;
  align-self: flex-start;
}

.navigator {
  height: 200px;
  display: flex;
  font-size: 18px;
  font-weight: 600;

  margin: 0 auto;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  text-decoration: none;
  list-style: none;
}
.navigator a {
  margin-left: -50px;
  transition: 0.5s;
}
.navigator a:hover {
  transform: scale(1.1);
}

.rightMenu {
  width: 100%;
  overflow: hidden;
  border-radius: 20px;
  flex: 3 1;
  padding: 25px;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  text-align: center;
  background-color: white;
}

.btnSettings {
  width: 45px;
  height: 45px;
  border: 1px solid grey;
  color: grey;
  background-color: unset;
  border-radius: 100%;
}

.loginBtn {
  width: 100%;
  background-color: #e99af2;

  color: white;
  font-weight: 700;
  border: 1px solid #ccc;
}
.facebookBtn {
  margin-top: 5px;
  width: 100%;
  background-color: #2f55a4;
  color: white;
  font-weight: 700;
}

.logout {
  cursor: pointer;
  font-weight: 500;
}
.register:hover {
  -webkit-text-decoration: underline black;
          text-decoration: underline black;
}

.logout {
  cursor: pointer;
  color: grey;
  font-weight: 500;
}
.logout:hover {
  -webkit-text-decoration: underline grey;
          text-decoration: underline grey;
}

.notifications {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 100%;
  height: 100%;
  transition: 0.5s;
}
.notifications span {
  margin: 10px;
  color: grey;
  cursor: pointer;
}
.notifications:hover {
  color: white;
}
.notifications span:hover {
  color: rgb(255, 255, 255);

  background-color: #57a1f9;
  background-image: linear-gradient(62deg, #57a1f9 0%, #e99af2 100%);
  border-radius: 100%;
}

.btnToggle {
  display: none;
}
@media only screen and (max-width: 768px) {
  .LayoutBackgorund {
    position: relative;
  }
  .btnToggle {
    display: block;
    position: fixed;
    top: 12px;
    width: 60px;
    height: 60px;

    border-radius: 100px;
    left: 12px;
    font-size: 38px;
    color: rgb(255, 255, 255);
    z-index: 5;
    background-image: linear-gradient(62deg, #57a1f9 0%, #e99af2 100%);

    text-align: center;
  }
  .leftMenu {
    background-color: #e5c2e9;
    display: none;
    position: fixed;
    justify-content: flex-start;
    border-radius: 0;
    z-index: 45;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
  }

  .Menu {
    padding: 10px 55px;
  }

  .navigator {
    justify-content: space-evenly;
  }

  .rightMenu {
    width: 100%;
    height: 100%;

    border-radius: 0px;
    min-height: 100vh;
  }
  .transperantBackground {
    border-radius: 0px;

    height: 100%;
    width: 100%;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 770px) {
  .rightMenu {
    width: 100%;
    height: 100%;

    border-radius: 0px;
    min-height: 100vh;
  }
  .transperantBackground {
    border-radius: 0px;

    height: 100%;
    width: 100%;
  }
}

.regForm {
  margin: 0 auto;
  display: flex;
  width: 50%;
  height: 100%;
  padding: 25px;
  justify-content: space-between;
  flex-direction: column;
  background-image: linear-gradient(62deg, #57a1f9 0%, #e99af2 100%);
  border-radius: 15px;
}
.regForm label {
  border-radius: 15px;
  height: 55px;
  margin-bottom: 10px;
  color: white;
  font-weight: 700;
  text-shadow: 3px 3px 15px black;
}

.regForm input {
  border-radius: 15px;
  height: 55px;
  margin-bottom: 10px;
  text-align: center;
}
.regForm span {
  height: 55px;

  border-radius: 15px;
  padding-top: 7px;
  text-align: center;
}
.regForm span input {
  text-align: center;
  height: 40px;
}

.regForm div {
  height: 35px;
  border-radius: 15px;
  margin-bottom: 10px;
  padding: 5px;
  margin: 0 auto;
  width: 80%;
}

.regForm input[type="submit"] {
  width: 100%;
  background-color: #e99af2;
  cursor: pointer;
  color: white;
  font-weight: 700;
  border: none;
}

.regForm input[type="submit"]:hover {
  background-color: white;
  border: 1px solid #e99af2;
  color: #e99af2;
}

@media only screen and (max-width: 768px) {
  .regForm {
    width: 100%;
  }
}

.root {
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.swipe {
  position: absolute;
}

.cardContainer {
  width: 90vw;
  max-width: 350px;
  height: 0px;
  margin: 25px auto;
  height: 600px;
}

.card {
  position: relative;
  background-color: #fff;
  width: 80vw;
  max-width: 260px;
  height: 400px;
  margin: 0 auto;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  border: 1px solid #ccc;
  display: flex;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.swipe:last-of-type {
}

.card h3 {
  background-color: white;
  width: 100%;

  justify-content: flex-end;
  align-content: flex-end;
  align-self: flex-end;
  justify-self: flex-end;
  padding: 25px;
  margin-bottom: -5px;

  color: rgb(141, 87, 230);
}

.infoText {
  width: 100%;
  height: 28px;
  justify-content: center;
  display: flex;
  color: rgb(49, 49, 49);
  animation-name: popup;
  animation-duration: 800ms;
}

.buttons {
  margin: 20px;
  margin-top: 50px;
  display: flex;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(203, 150, 228) 27.3%,
    rgb(121, 131, 223) 112.7%
  );
  border-radius: 15px;
  padding: 25px;
}

.swipeRight {
  margin: 10px;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(68, 223, 138) 27.3%,
    rgb(40, 172, 57) 112.7%
  );
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border: none;
  font-weight: 700;
  cursor: pointer;
  color: white;
  font-weight: 700;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.swipeRight:hover {
  transform: scale(1.05);
  color: white;
  border: 1px solid white;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(203, 150, 228) 27.3%,
    rgb(121, 131, 223) 112.7%
  );
}
.swipeLeft {
  margin: 10px;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(231, 128, 180) 27.3%,
    rgb(241, 63, 63) 112.7%
  );
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border: none;
  font-weight: 700;
  cursor: pointer;
  color: white;
  font-weight: 700;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.swipeLeft:hover {
  color: white;
  transform: scale(1.05);
  border: 1px solid white;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(203, 150, 228) 27.3%,
    rgb(121, 131, 223) 112.7%
  );
}

.startmatchAgain {
  color: white;
  background-color: #8bc6ec;
  background-image: linear-gradient(
    111.6deg,
    rgb(203, 150, 228) 27.3%,
    rgb(121, 131, 223) 112.7%
  );
  margin: 15px;
  width: 100%;
  height: 60px;
  font-size: 20px;
  text-align: center;
  border-radius: 15px;
}
@keyframes popup {
  0% {
    transform: scale(1, 1);
  }
  10% {
    transform: scale(1.1, 1.1);
  }
  30% {
    transform: scale(0.9, 0.9);
  }
  50% {
    transform: scale(1, 1);
  }
  57% {
    transform: scale(1, 1);
  }
  64% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

@media only screen and (max-width: 768px) {
  .cardContainer {
    height: 300px;
  }
  .card {
    height: 200px;
  }
  .infoText {
    height: 18px;
  }
  .card h3 {
    padding: 10px;
  }

  .startmatchAgain {
    height: 40px;
    font-size: 15px;
  }
  .infoText {
    width: 100%;
    height: 25px;

    font-size: 13px;
  }
}

.HomePageContainer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
  gap: 15px;
  overflow: hidden;
}
.contentStyle {
  grid-column: 1/3;
  width: 100%;
  color: rgb(255, 255, 255);
  text-shadow: 3px 3px 15px rgb(100, 100, 100);

  font-size: 30px;
  line-height: 160px;
  text-align: center;
  background-color: #57a1f9;
  background-image: linear-gradient(180deg, #f0acea 0%, #ffffff 100%);
  padding: 100px 0px;
  font-family: "Michroma", sans-serif;
  display: flex;
  flex-direction: column;
}
.contentStyle h1 {
  color: white;
  font-size: 40px;
  font-weight: 700;
}
.contentStyle h5 {
  color: white;
  font-weight: 700;
  font-size: 40px;
}

.carouselDIV {
  grid-column: 1/3;
  width: 100%;
  margin: 0 auto;
  max-width: 900px;
  overflow: hidden;
}
.signUpNow {
  width: 100%;
  height: 100%;

  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.signUpNow h1 {
  font-size: 55px;
  font-family: "Ubuntu", sans-serif;
}
.signUpNow button {
  width: 200px;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
  height: 80px;
  background-color: transparent;
  border-radius: 15px;
  font-size: 25px;
  color: rgb(187, 98, 199);
  border: 3px solid rgb(187, 98, 199);
  transition: 0.5s;
}
.signUpNow button:hover {
  border: 3px solid white;
  background-color: rgb(187, 98, 199);
  color: white;
}
.latestUsers {
  width: 80%;
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  padding: 15px;
}
.latestUsers img {
  transition: 0.5s;
}
.latestUsers img:hover {
  transform: scale(1.1);
}
.latestUsers p {
  color: grey;
  font-weight: 400;
}

@media only screen and (max-width: 768px) {
  .HomePageContainer {
    grid-template-columns: 1fr;
  }

  .contentStyle {
    grid-column: 1;
    text-align: center;
    font-size: 15px;
    line-height: 160px;
  }
  .contentStyle h1 {
    font-size: 18px;
    font-weight: 700;
    margin-left: -25px;
  }
  .contentStyle h5 {
    font-size: 18px;
    margin-left: -15px;
  }
  .carouselDIV {
    grid-column: 1;
  }
  .latestUsers {
    grid-column: 1;
    width: 100%;
    height: 100%;
  }
}

